
Template.__checkName("rooms");
Template["rooms"] = new Template("Template.rooms", (function() {
  var view = this;
  return [ HTML.Raw("<p>\n    Rooms:\n  </p>\n  "), HTML.FORM({
    "class": "new-room"
  }, "\n    ", HTML.Raw('<input type="text" name="text" placeholder="Enter room name">'), "\n\n    ", HTML.LABEL(HTML.INPUT({
    type: "checkbox",
    checked: function() {
      return Spacebars.mustache(view.lookup("checked"));
    },
    "class": "toggle-checked"
  }), "Private"), "\n\n  "), "\n\n  ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("rooms"));
  }, function() {
    return [ "\n      ", HTML.P({
      "class": "room"
    }, Blaze.View("lookup:name", function() {
      return Spacebars.mustache(view.lookup("name"));
    }), " ", Blaze.View("lookup:isOwner", function() {
      return Spacebars.mustache(view.lookup("isOwner"));
    }), " "), "\n  " ];
  }) ];
}));
