
Template.__checkName("messagesBlock");
Template["messagesBlock"] = new Template("Template.messagesBlock", (function() {
  var view = this;
  return [ HTML.DIV({
    "class": "messages_block"
  }, "\n      ", Blaze.Each(function() {
    return Spacebars.call(view.lookup("messages"));
  }, function() {
    return [ "\n        ", Spacebars.include(view.lookupTemplate("message")), "\n      " ];
  }), "\n  "), HTML.Raw('\n  <form class="new-message">\n    <input type="text" name="message" class="message" placeholder="Type new message">\n  </form>') ];
}));

Template.__checkName("message");
Template["message"] = new Template("Template.message", (function() {
  var view = this;
  return HTML.DIV({
    "class": "message"
  }, "\n      ", HTML.A({
    "class": "message_username"
  }, Blaze.View("lookup:username", function() {
    return Spacebars.mustache(view.lookup("username"));
  })), " ", HTML.SPAN({
    "class": "message_timestamp"
  }, Blaze.View("lookup:dateFormat", function() {
    return Spacebars.mustache(view.lookup("dateFormat"), view.lookup("createdAt"));
  })), "\n      ", HTML.DIV("\n        ", HTML.A({
    "class": "message_text"
  }, Blaze.View("lookup:text", function() {
    return Spacebars.mustache(view.lookup("text"));
  })), "\n      "), "\n    ");
}));
