
Template.__checkName("spot");
Template["spot"] = new Template("Template.spot", (function() {
  var view = this;
  return HTML.P(HTML.Raw('\n    <p>\n      Spot Info\n      <button class="addSpot">Edit spot info</button>\n      <button class="deleteSpot">Delete spot</button>\n    </p>\n    '), Blaze.If(function() {
    return Spacebars.call(Spacebars.dot(view.lookup("spot"), "editing"));
  }, function() {
    return [ "\n    ", HTML.LI("\n      SpotID: ", Blaze.View("lookup:spot._id", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "_id"));
    }), "\n    "), "\n    ", HTML.LI("\n      UserID: ", Blaze.View("lookup:spot.user", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "user"));
    }), "\n    "), "\n    ", HTML.FORM({
      "class": "new-spot"
    }, "\n      ", HTML.LI("\n        Spot Name", HTML.INPUT({
      type: "text",
      name: "spotName",
      placeholder: function() {
        return Blaze.If(function() {
          return Spacebars.call(Spacebars.dot(view.lookup("spot"), "name"));
        }, function() {
          return [ " ", Blaze.View("lookup:spot.name", function() {
            return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "name"));
          }), " " ];
        }, function() {
          return " Type new ";
        });
      }
    }), "\n      "), "\n      ", HTML.LI("\n        Spot Type", HTML.INPUT({
      type: "text",
      name: "spotType",
      placeholder: function() {
        return Blaze.If(function() {
          return Spacebars.call(Spacebars.dot(view.lookup("spot"), "type"));
        }, function() {
          return [ " ", Blaze.View("lookup:spot.type", function() {
            return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "type"));
          }), " " ];
        }, function() {
          return " Add type of spot ";
        });
      }
    }), "\n      "), "\n      ", HTML.LI("\n        Description ", HTML.INPUT({
      type: "text",
      name: "description",
      placeholder: function() {
        return Blaze.If(function() {
          return Spacebars.call(Spacebars.dot(view.lookup("spot"), "descr"));
        }, function() {
          return [ " ", Blaze.View("lookup:spot.descr", function() {
            return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "descr"));
          }), " " ];
        }, function() {
          return " Add description ";
        });
      }
    }), "\n      "), "\n      ", HTML.INPUT({
      type: "submit",
      value: "Submit"
    }), "\n    "), "\n    " ];
  }, function() {
    return [ "\n    ", HTML.LI("\n      SpotID: ", Blaze.View("lookup:spot._id", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "_id"));
    }), "\n    "), "\n    ", HTML.LI("\n      UserID: ", Blaze.View("lookup:spot.user", function() {
      return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "user"));
    }), "\n    "), "\n    ", Blaze.If(function() {
      return Spacebars.call(Spacebars.dot(view.lookup("spot"), "name"));
    }, function() {
      return [ "\n    ", HTML.LI("\n      Spot Name ", Blaze.View("lookup:spot.name", function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "name"));
      }), "\n    "), "\n    " ];
    }), "\n    ", Blaze.If(function() {
      return Spacebars.call(Spacebars.dot(view.lookup("spot"), "type"));
    }, function() {
      return [ "\n    ", HTML.LI("\n      Spot Type ", Blaze.View("lookup:spot.type", function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "type"));
      }), "\n    "), "\n    " ];
    }), "\n    ", Blaze.If(function() {
      return Spacebars.call(Spacebars.dot(view.lookup("spot"), "descr"));
    }, function() {
      return [ "\n    ", HTML.LI("\n      Spot Description ", Blaze.View("lookup:spot.descr", function() {
        return Spacebars.mustache(Spacebars.dot(view.lookup("spot"), "descr"));
      }), "\n    "), "\n    " ];
    }), "\n    " ];
  }), "\n\n  ");
}));
