
Template.__checkName("users");
Template["users"] = new Template("Template.users", (function() {
  var view = this;
  return [ HTML.Raw('<p>\n    Users:\n  </p>\n  <form class="user">\n    <input type="text" name="user" placeholder="find users">\n  </form>\n  '), Blaze.Each(function() {
    return Spacebars.call(view.lookup("users"));
  }, function() {
    return [ "\n    ", HTML.LI("\n      ", Blaze.View("lookup:username", function() {
      return Spacebars.mustache(view.lookup("username"));
    }), "\n    "), "\n  " ];
  }) ];
}));
