
Template.__checkName("chat");
Template["chat"] = new Template("Template.chat", (function() {
  var view = this;
  return HTML.DIV({
    "class": "message_block"
  }, "\n    ", Blaze.If(function() {
    return Spacebars.call(view.lookup("social"));
  }, function() {
    return [ "\n    ", HTML.P("\n      ", HTML.SPAN(Spacebars.include(view.lookupTemplate("rooms"))), "\n      ", HTML.SPAN(Spacebars.include(view.lookupTemplate("users"))), "\n    "), "\n    " ];
  }, function() {
    return [ "\n      ", Spacebars.include(view.lookupTemplate("messagesBlock")), "\n    " ];
  }), "\n  ");
}));
